import React from 'react';
import styled from 'styled-components';
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import { List } from 'react-content-loader';

import { scheme } from '../../lib/theme';

import Community from '../community/community.component';

const Communities = styled.section`
  grid-area: communities;
  border-right: 1px solid ${scheme.gray[4]};
  display: flex;
  flex-direction: column;
`;

const Loading = styled.div`
  margin-top: 40px;
  padding-left: 40px;
`;

const ALL_COMMUNITIES_QUERY = gql`
  query ALL_COMMUNITIES_QUERY {
    communities {
      id
      name
      description
      picture
      likes
      threads_count
    }
  }
`;

export default props => (
  <Query query={ALL_COMMUNITIES_QUERY} fetchPolicy="cache-and-network" srr={true}>
    {({ data: { communities }, error, loading }) => {
      if (error) return <p>Error: {error.message}</p>;
      if (loading)
        return (
          <Loading>
            <List secondaryColor="#ced4da" primaryColor="#e9ecef" />
          </Loading>
        );
      if (communities && communities.length)
        return (
          <Communities>
            {communities.map(community => (
              <Community key={community.id} {...community} />
            ))}
          </Communities>
        );
    }}
  </Query>
);
